import React, { useEffect, useState, useRef } from "react";
import { useForm, ValidationError } from "@formspree/react";
import "../styles/contact.css";

const Contact = () => {
  const [state, handleSubmit] = useForm("xrbonjgb");
  const [showSuccess, setShowSuccess] = useState(false);
  const formRef = useRef(null);

  useEffect(() => {
    if (state.succeeded) {
      setShowSuccess(true);
      if (formRef.current) formRef.current.reset();
      const timer = setTimeout(() => setShowSuccess(false), 10000);
      return () => clearTimeout(timer);
    }
  }, [state.succeeded]);

  return (
    <section className="register-section flex-center" id="contact">
      {/* Liquid Glass Background */}
      <div className="bg">
        <div className="blob b1"></div>
        <div className="blob b2"></div>
        <div className="blob b3"></div>
        <div className="grain"></div>
      </div>

      <div className="contact-container flex-center contact">
        <h2 className="form-heading">Contact Us</h2>

        {showSuccess && (
          <div className="form-success animate-success">
            <p>Your message has been sent successfully!</p>
          </div>
        )}

        <form
          ref={formRef}
          onSubmit={handleSubmit}
          className="register-form"
          style={{ opacity: state.succeeded ? 0.8 : 1 }}
        >
          <div className="field">
            <input
              id="name"
              type="text"
              name="name"
              placeholder="Enter your name"
              required
            />
            <ValidationError prefix="Name" field="name" errors={state.errors} />
          </div>

          <div className="field">
            <input
              id="email"
              type="email"
              name="email"
              placeholder="Enter your email"
              required
            />
            <ValidationError prefix="Email" field="email" errors={state.errors} />
          </div>

          <div className="field">
            <textarea
              id="message"
              name="message"
              placeholder="Enter your message"
              rows="4"
              required
            ></textarea>
            <ValidationError prefix="Message" field="message" errors={state.errors} />
          </div>

          <button
            type="submit"
            className="glass-btn"
            disabled={state.submitting}
          >
            Send
          </button>
        </form>
      </div>
    </section>
  );
};

export default Contact;
